@-webkit-keyframes animspin {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes animspin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#app-loader {
  background: #222;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  min-height: 480px;
  z-index: 99999;
  transition: opacity 450ms;
}

#app-loader .icon {
  display: block;
  width: 100px;
  height: 100px;
  position: relative;
  top: 50%;
  margin: -50px auto 0;
  -webkit-animation: animspin 1000ms infinite linear;
  animation: animspin 1000ms infinite linear;
}

#app-loader .icon--path {
  fill: #fff;
}

.no-place-set {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999999;
  background: #000;
  color: #fff;
  padding: 20px;
  text-align: center;
  font-family: sans-serif;
  box-sizing: border-box;
}

.no-place-set h1 {
  font-size: 20px;
  margin-top: 20px;
}

.no-place-set .logo {
  margin-top: 50px;
  height: 50px;
  margin: 100px 0 20px;
  background-size: 100% auto;
  background: -owg-linear-gradient(#12ae6f, #57d4a2);
  -pie-background: -pie-linear-gradient(#12ae6f, #57d4a2);
  background: linear-gradient(#12ae6f, #57d4a2);
  -webkit-mask-image: url("/gfx/logo.png");
  -webkit-mask-size: 50px;
  -webkit-mask-position: center center;
  -webkit-mask-repeat: no-repeat;
  -moz-mask-image: url("/gfx/logo.png");
  -moz-mask-size: 50px;
  -moz-mask-position: center center;
  -moz-mask-repeat: no-repeat;
  -webkit-mask-image: url("/gfx/logo.png");
          mask-image: url("/gfx/logo.png");
  -webkit-mask-size: 50px;
          mask-size: 50px;
  -webkit-mask-position: center center;
          mask-position: center center;
  -webkit-mask-repeat: no-repeat;
          mask-repeat: no-repeat;
}

.no-place-set .sub {
  font-size: 17px;
  line-height: 20px;
  color: #d0d0d0;
}

